body {
  overflow-x: hidden;
  /* font-family: "Gaegu"; */
}

/* .form-control {
  font-size: larger;
} */

.btn-primary,
.btn-success,
.btn-danger {
  font-size: 17px;
}

/* Container to position the hamburger on the top left */
.hamburger-container {
  position: fixed;
  top: 20px;
  left: 20px;
  z-index: 7;
}

/* Hamburger icon styling */
.hamburger-icon {
  width: 30px;
  height: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  z-index: 8;
  position: relative;
}

.hamburger-icon span {
  display: block;
  background: linear-gradient(to right, #ff3100, #4800ff);
  height: 4px;
  border-radius: 2px;
  transition: all 0.3s ease;
}

.open span {
  /* display: block; */
  background: linear-gradient(to left, #ff3100, #4800ff);
}

/* Animation for the open state */
.hamburger-icon.open span:nth-child(1) {
  transform: translateY(10px) rotate(45deg);
}

.hamburger-icon.open span:nth-child(2) {
  opacity: 0;
}

.hamburger-icon.open span:nth-child(3) {
  transform: translateY(-10px) rotate(-45deg);
}

/* Menu styling */
.menu {
  position: fixed;
  top: 0;
  left: -100%;
  height: 100vh;
  /* width: 350px; */
  background-color: #333;
  color: white;
  padding-top: 60px;
  transition: left 0.3s ease;
  z-index: 5;
}

.menu ul {
  list-style: none;
  padding: 0;
}

.menu ul li {
  font-family: "Poppins";
  font-weight: 500;
  padding: 15px 20px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.menu ul li:hover {
  background-color: #444;
}

/* Open menu animation */
.menu-open {
  left: 0;
  width: fit-content;
}

.home-bg {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100vw;
}

.bg-black {
  background-color: black;
}

.full-wh {
  position: absolute;
  top: 0px;
  left: 0px;
  bottom: 0px;
  width: 100%;
}

.bg-animation {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

