/* Introduction text container slides to the right */
@keyframes slideOutRight {
  from {
    transform: translateX(0);
    opacity: 1;
  }
  to {
    transform: translateX(100%); /* Moves the text container out to the right */
    opacity: 0;
  }
}

/* UserForm slides in from the right side to the left */
@keyframes slideInRight {
  from {
    transform: translateX(-100%); /* Starts from offscreen on the right */
    opacity: 0;
  }
  to {
    transform: translateX(0); /* Brings the form into view */
    opacity: 1;
  }
}

/* Add class to apply sliding out animation to the Introduction Text */
.slide-out {
  animation: slideOutRight 1s forwards;
}

/* Add class to apply sliding in animation to the UserForm */
.slide-in-left {
  animation: slideInLeft 1s forwards;
}

