  width: 5px;
  height: 0;
  background:#faaa39;
}

.top {
  animation: animateTop 3s ease-in-out infinite;
}

.bottom {
  animation: animateBottom 3s ease-in-out infinite;
}

.right {
  animation: animateRight 3s ease-in-out infinite;
}

.left {
  animation: animateLeft 3s ease-in-out infinite;
}

@keyframes animateTop {
  25% {
    width: 100%;
    opacity: 1;
  }

  30%,
  100% {
    opacity: 0;
  }
}

@keyframes animateBottom {
  0%,
  50% {
    opacity: 0;
    width: 0;
  }

  75% {
    opacity: 1;
    width: 100%;
  }

  76%,
  100% {
    opacity: 0;
  }
}

@keyframes animateRight {
  0%,
  25% {
    opacity: 0;
    height: 0;
  }

  50% {
    opacity: 1;
    height: 100%;
  }

  55%,
  100% {
    height: 100%;
    opacity: 0;
  }
}

@keyframes animateLeft {
  0%,
  75% {
    opacity: 0;
    bottom: 0;
    height: 0;
  }

  100% {
    opacity: 1;
    height: 100%;
  }
}


.data-container {
  border-top: .5px solid #faaa39;
  border-right: .5px solid #faaa39;
  border-bottom: .5px solid #faaa39;
  border-left: .5px solid #faaa39;
  height: fit-content;
  min-width: fit-content;
  text-align: center;
  /* border: 1px solid red; */
}

.data-container-head {
  font-family: "Poppins", sans-serif;
  font-size: 22px;
}

.f-poppins {
  font-family: "Poppins";
}

/* Sun glide, grow, and fade-out animation */
@keyframes sunAnimation {
  0% {
    left: -55%;
    transform: scale(1); /* Start from its initial position */
    opacity: 1;
    z-index: 999;
  }
  50% {
    left: 0%;
    transform: scale(1); /* Move to center and start growing */
    z-index: 999;
    opacity: 1;
  }
  100% {
    transform: translateX(0) scale(10); /* Expand to fill the screen */
    opacity: 0; /* Fade out */
    display: none; /* Optionally hide at the end */
    z-index: 999;
  }
}

/* Class to trigger the animation */
.sun-animate {
  animation: sunAnimation 3s forwards ease-in-out;
}
.solar-system-container {
  display: none; /* Initially hidden */
}

.solar-system-container.visible {
  display: block; /* Show when the class is added */
}

@keyframes scaleAnimation {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

.scale-animation {
  animation: scaleAnimation 2s forwards; /* Animation applied here */
}

.solar-system-container {
  animation: scaleAnimation 2s ease-out forwards;
  transform-origin: center;
}

/* Planets shrink to 0 and then disappear */
@keyframes shrinkAndDisappear {
  0% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(0);
  }
}

.shrink-and-disappear {
  animation: shrinkAndDisappear 2s forwards;
}

